import { useMemo, useState } from "react";
import { useDebounce } from "./useDebounce";
export function useAccountSearch(accounts, debounceTime = 300, searchFields = ["name", "address", "sphincsPlusPubKey"]) {
    const [searchTerm, setSearchTerm] = useState("");
    const debouncedSearchTerm = useDebounce(searchTerm, debounceTime);
    const filteredAccounts = useMemo(() => {
        if (!debouncedSearchTerm.trim()) {
            return accounts;
        }
        const searchTermLower = debouncedSearchTerm.toLowerCase();
        return accounts.filter((account) => {
            return searchFields.some((field) => {
                const value = account[field];
                return value && String(value).toLowerCase().includes(searchTermLower);
            });
        });
    }, [accounts, debouncedSearchTerm, searchFields]);
    const handleSearch = (value) => {
        setSearchTerm(value);
    };
    return {
        searchTerm,
        debouncedSearchTerm,
        filteredAccounts,
        handleSearch,
    };
}
export default useAccountSearch;
